
.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto; 
  background-color:transparent;
  
}

.button-hamburgerIcon .img-hamburger{
    height: 32px;
    vertical-align: middle;
}

.button-hamburgerIcon:active{
      transform: scale(0.90);
}



.div-menu{
  margin-top: 18px;
  background-color: #14343f;
  width: 100vw;
  margin-left: calc(-100vw + 100%);
}
.div-menu ul{
  list-style: none;
  overflow: hidden;
}

.div-menu ul li{
  color: #fff;
  margin: 0px;
  padding: 0;

}

.div-menu ul li a{
  text-decoration: none;
  color: #fff;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 20px 32px ;
  font-size: 20px;
  letter-spacing: 1.5px;
  
}
.div-menu ul li .suport{
  float: left;
}
.div-menu ul li .iconPregunta{
  float: left;
  display:inline-block;
  padding-right: 10px;
  background-color: none;

}

.div-menu ul li a:hover{
  text-decoration: underline #fff;
  background-color:#0b1b20;
}
.div-menu ul li .learn{
  background-color:#4ea6a6 ;
  color: #d9fcfc;
  width:200px;
  height: auto;
  border-radius: 7px;
  font-size: 19.7px;
  font-family: "Press Start 2P";
  letter-spacing: 0px; 
  text-shadow: 4px 1.5px 1px #2e4a58;
  padding: 17px 10px 14px 10px;
  box-shadow: 6px 7px #2e4a58;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top:10px;
  margin-bottom: 20px;
  margin-left: 50px;
  margin-right: auto 
  
}
.div-menu ul li .learn:hover{
  text-decoration: none;
  background-color:#4ea6a6;

}
.div-menu ul li .learn:active{
  color: #54b4b4;
  background-color: #2e4a58;
  box-shadow: 0 2px #54b4b4;
  transform: translateY(4px);
  transition-duration: 0.03s;
}
.div-menu ul .li-learn:hover{
  text-decoration: underline #fff 3.5px;
}
.div-menu ul .donate{
  
  font-family: 'Open Sans', sans-serif;
}
.div-menu .separador{
  height: 100vw;
  opacity: 0.5;
  background: #000;
 
}

