}

*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    
}
/** @format Nabvar*/

* {
  margin: 0;
  font-family: "Noto Sans", sans-serif;
}
.header-navbar {
  top: 0;
  height: 64px;
  -webkit-align-items: center;
          align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  z-index: 100;
  display: -webkit-flex;
  display: flex;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
  object-fit: contain;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
 padding: auto;

}
.header-navbar .navbarDiv2 StarButton{
  display: block;
  object-fit: contain;
  
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;

}










/** @format */
.starInput {
  background-color: #ffc500;
  color: #fff;
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P", cursive;
  letter-spacing: 3px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto 
}

.starInput:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}
.starInput:hover{
  
}
.div-starbutton{
  display: block;
  float: right;
  width: 100%;
}


.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto; 
  background-color:transparent;
  
}

.button-hamburgerIcon .img-hamburger{
    height: 32px;
    vertical-align: middle;
}

.button-hamburgerIcon:active{
      -webkit-transform: scale(0.90);
              transform: scale(0.90);
}



.div-menu{
  margin-top: 18px;
  background-color: #14343f;
  width: 100vw;
  margin-left: calc(-100vw + 100%);
}
.div-menu ul{
  list-style: none;
  overflow: hidden;
}

.div-menu ul li{
  color: #fff;
  margin: 0px;
  padding: 0;

}

.div-menu ul li a{
  text-decoration: none;
  color: #fff;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 20px 32px ;
  font-size: 20px;
  letter-spacing: 1.5px;
  
}
.div-menu ul li .suport{
  float: left;
}
.div-menu ul li .iconPregunta{
  float: left;
  display:inline-block;
  padding-right: 10px;
  background-color: none;

}

.div-menu ul li a:hover{
  -webkit-text-decoration: underline #fff;
          text-decoration: underline #fff;
  background-color:#0b1b20;
}
.div-menu ul li .learn{
  background-color:#4ea6a6 ;
  color: #d9fcfc;
  width:200px;
  height: auto;
  border-radius: 7px;
  font-size: 19.7px;
  font-family: "Press Start 2P";
  letter-spacing: 0px; 
  text-shadow: 4px 1.5px 1px #2e4a58;
  padding: 17px 10px 14px 10px;
  box-shadow: 6px 7px #2e4a58;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top:10px;
  margin-bottom: 20px;
  margin-left: 50px;
  margin-right: auto 
  
}
.div-menu ul li .learn:hover{
  text-decoration: none;
  background-color:#4ea6a6;

}
.div-menu ul li .learn:active{
  color: #54b4b4;
  background-color: #2e4a58;
  box-shadow: 0 2px #54b4b4;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}
.div-menu ul .li-learn:hover{
  -webkit-text-decoration: underline #fff 3.5px;
          text-decoration: underline #fff 3.5px;
}
.div-menu ul .donate{
  
  font-family: 'Open Sans', sans-serif;
}
.div-menu .separador{
  height: 100vw;
  opacity: 0.5;
  background: #000;
 
}

/*icon pregunta*/
.a-home_link{
    height: 100%;
    width: 70px;
    border: none;
    cursor: pointer;
    outline: none;
    display: block;
    margin-left: 32px;
    margin-right: auto 
  }
  .a-home_link .img-home-link {
    height: 60px;
    width: auto;
    margin-top: -5px;
    display: inline-block;
    vertical-align: middle;
    -webkit-transform:perspective(1px)translateZ(0);
    transition-duration: 0.2s;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;

  }
  .a-home_link .img-home-link:active{
      -webkit-transform: scale(0.85);
      transform: scale(0.85);
  }
